*{
  box-sizing: border-box;
  margin: 0px;
}
button{
  cursor: pointer;
}
.header
{
  margin-top: 10px;
  margin-inline: 78px;
  max-height: 40px;
  min-width: 280px;
  font-size: 14px;
  display: flex;
  align-items: center;
  gap: 20px;
  justify-content: space-between;
  font-weight: 540; 
  font-family: 'Segoe', 'Segoe UI', Arial, 'sans-serif';

}
.main_logo
{
  height: 24px;
}
.header_left{
  display: flex;
  align-items: center;
  gap: 20px;
}
nav
{
  display: flex;
  gap: 18px;
  color: #262626;
  cursor: pointer;
}


.header_right
{
  display: flex;
  align-items: center;
  cursor: pointer;
  gap: 20px;
}
.header_right_nav_image{
  display: flex;
  align-items: center;
  gap: 5px;
  
}
.down_arrow
{
  top: 0px;
  height: 20px;
  cursor: pointer;
}
.search_icon
{
  top: 0px;
  height: 20px;
  cursor: pointer;
}
.user_icon
{
  top: 0px;
  height: 30px;
  cursor: pointer;
}
.body
{
  display: flex;
  align-items: center;
  justify-content: center;

  min-height: 650px;
  gap: 50px;
  padding: 50px 0 50px;
}
.body_center{
  max-width: 450px ;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 33px 0px 20px 0px;
  justify-content: space-between;
  flex-wrap: wrap;
}
.body_center_up
{
  display: flex;
  flex-direction: column;
  align-items: center;
}
.welcome_left
{
  height: 478px;
  padding: 102px 16px 0;
}
.welcome_right
{
  height: 525px;
  padding: 44px 0 0 55px;

}
.logo{
  
  height: 79px;
}

.body_title{
  margin-top: 20px;
  font-size: 38px;
  font-weight: 200;
  font-family: 'Segoe', 'Segoe UI', Arial, 'sans-serif';

}
.body_content
{
  margin: 20px 0px 16px 0px ;
  font-size: 28px;
  font-family: 'Segoe', 'Segoe UI', Arial, 'sans-serif'; 
  text-align: center;
}

.body_center_center
{
  padding: 20px 0 60px;
  text-align: center;
  gap: 20px;
}
.getStarted {
  padding: 14px 45px;
  background: #0078d4;
  color: white;
  font-weight: 600;
  font-size: 18px;
  border-radius: 2px;
  margin-bottom: 1.6rem;
  cursor: pointer;
  
}
.learn
{
  max-width: 354px;
    font-size: 20px;
    color: #0071c8;
    cursor: pointer;
    font-family: 'Segoe', 'Segoe UI', Arial, 'sans-serif';
}

.icons
{
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-bottom: 20px;
}

.soft_icons
{
  width: 47px;
  height: 37px;
  padding: 4px;
  border:  1px solid ;
  cursor: pointer;
}
.d_icons
{
  display: flex;
  max-width: 354px;

  gap: 10px;
}
.body_download
{
  font-size: 19px;
  text-align: center;
}
.terms
{
  margin-top: 10px;
  font-size: 19px;
}
.home_footer
{
  background-color: rgb(232, 232, 232);
  max-width:100%;
  height: 437px;
  padding: 50px;

 
}
.footer_1
{
  display: flex;
  justify-content: space-between;
}
.footer_title
{
  font-size: 17px;

}
.footer_content
{
  font-size: 12px;
}
.footer_
{
  display: flex;
  flex-direction: column;
  gap: 20px;
  justify-content: center;
}
.footer_2
{
  margin-top: 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
}
.footer_2_left
{
  display: flex;
  gap: 20px;
}
.footerleft
{
  display: flex;
  align-items: center;
  font-size: 13px;
  gap: 9px;
}
.country
{
  width: 40px;
  height: 40px;
}
.footerleft_1
{
  display: flex;
  align-items: center;
}
.privacy
{
  width: 30px;
  height: 25px;
}
.footer_2_right
{
  display: flex;
  font-size: 13px;
  gap: 20px;
}