.signIn_Page
{
  width: 100%;
  min-height: 100vh;
  background:url(https://aadcdn.msftauth.net/shared/1.0/content/images/backgrounds/2_11d9e3bcdfede9ce5ce5ace2d129f1c4.svg);
  display: flex;
  align-items: center;
  justify-content: center;
}
.signIn_card
{
 
    position: relative;
    max-width: 440px;
    width: calc(100% - 40px);
    padding: 44px;
    margin-bottom: 28px;
    background-color: #fff;
    -webkit-box-shadow: 0 2px 6px rgba(0,0,0,0.2);
    -moz-box-shadow: 0 2px 6px rgba(0,0,0,0.2);
    box-shadow: 0 2px 6px rgba(0,0,0,0.2);
    min-width: 320px;
    min-height: 338px;
    overflow: hidden;

}
.sigin_title
{
  font-size: 24px;
  margin-block: 15px;
  color: #1B1B1B;
  font-weight: 540;
}
input[type=text]
{
  font-size: 14px;
}
.email_box
{
  width: 348px;
  height: 36px;
  border-bottom: 1px solid;
  border-top: none;
  border-left: none;
  border-right: none;
  margin-bottom: 15PX;
}
.accounts
{
  display: flex;
  gap: 15px;
  margin-block: 10px;
}
.create_account
{
  color: #0067b8;
  cursor: pointer;
}
.cant_access
{
  margin-block: 20px;
  color: #0067b8;
  cursor: pointer;
}
.buttons
{
  margin-block: 20px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  gap: 10px;
}
.back
{
 
    padding: 4px 12px 4px 12px;
    width: 108px;
    height: 32px;
    background-color: #ccc;
    background-color: rgba(0,0,0,0.2);
    cursor: pointer;
}
.Next
{
  color: #fff;
  width: 108px;
  height: 32px;
    border-color: #0067b8;
    background-color: #0067b8;
    display: block;

}