.important_page
{
  width: 100%;
  padding: 20px;

}

.important_title
{
  color: #2564cf;
}
.important_page_up
{
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.star_icon
{
  width: 26px;
  height:26px;
}
.three_dot
{
  width: 24px;
  height: 23px;
}
.important_headline
{
  display: flex;
  align-items: center;
  gap: 20px;
}
.important_sort1
{
  width: 25px;
  height: 25px;
}
.important_sort
{
  display: flex;
  align-items: center;
  color: #2564cf;
  font-size: 20px;
}
.important_page_down
{
 
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.important_content_up {
  width: 100%;
  min-height: 52px;
  display: flex;
 
}
.important_page_content {
  margin-top: 30px;
  border: 1px solid white;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.2);
}
.important_input {
  width: 100%;
  padding: 10px;
  border: none;
  font-size: 16px;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  transition: border-color 0.3s ease;
}

.important_input:focus {
  outline: none;
  border-color: transparent;
}
.important_page_down {
  padding: 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
  background-color: whitesmoke;
  min-height: 52px;
}
.important_down_left
{
  display: flex;
  gap: 19px;
}
.star_icon_task
{
  width: 25px;
  height: 25px;
}
.task_container_up
{
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.container_task_title
{
  display: flex;
  align-items: center;
  gap: 20px;
  margin: 5px;
}
.delete_task
{
  width: 60px;
  height: 30px;
  background-color: lavenderblush;
  border-radius: 3px;
  border: none;
  margin-top: 5px;
}