.sidebar
{

    width: 290px;
    min-height: 100vh;
    background-color: #fff;
    border: 2px solid white;
    box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.2); 
 
  


}
.task_.active {
  background-color: #f0f0f0; 
  border-left: 3px solid #007bff; 
}
.task_
{
  width: 290px;
  height:44px;
  display: flex;
  gap: 30px;
  padding: 12px 24px;
  margin: 10px;

}
._icon
{
  width: 20px;
  height: 20px;
}
.menu_icon
{
  padding: 0px 24px;
  margin-top: 20px;
}
.my_day
{
  cursor: pointer;
}