  /* Existing styles */
  .task_title {
    display: flex;
    align-items: center;
    gap: 10px;
  }

  .task_toolbar_headline {
    width: 100%;
    display: flex;
    justify-content: space-between;
  }

  .sun_icon {
    height: 26px;
    width: 26px;
  }

  .myDay {
    height: 100vh;
    padding: 30px;
    width: 100%;
  }

  .myday {
    font-size: 23px;
    font-weight: 500;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  }

  .option_icon {
    width: 25px;
    height: 20px;
  }

  .sort {
    width: 25px;
    height: 25px;
  }

  .task_tool_right {
    display: flex;
    gap: 25px;
  }

  .task_sort {
    display: flex;
    gap: 5px;
    align-items: center;
  }

  .task_toolbar_content {
    margin-top: 30px;
    border: 1px solid white;
    box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.2);
  }

  .myday_content_down {
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
    background-color: whitesmoke;
    min-height: 52px;
  }

  .bell_icon {
    width: 25px;
    height: 25px;
  }

  .myday_content_down_left {
    display: flex;
    align-items: center;
    gap: 20px;
  }

  .add_task {
    height: 32px;
    width: 43px;
    background-color: rgba(198, 158, 112, 0.2);
    border: none;
  }

  .myday_content_up {
    width: 100%;
    min-height: 52px;
    display: flex;
  }

  .myday_input {
    width: 100%;
    padding: 10px;
    border: none;
    font-size: 16px;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    transition: border-color 0.3s ease;
  }

  .myday_input:focus {
    outline: none;
    border-color: transparent;
  }

  /* Styles for the task container */
  .task_container {
    margin-top: 20px;
    border: 1px solid white;
    box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.2);
    padding: 10px;
    
    
  }
.task_container_up
{
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 30px;
}
  .task_container h2 {
    font-size: 18px;
    margin-bottom: 10px;
  }

  .task_container ul {
    list-style-type: none;
    padding: 0;
  }

  .task_container li {
    font-size: 16px;
    margin-bottom: 5px;
  }

  /* Style for the button in myday_content_down_right */
  .myday_content_down_right button {
    height: 32px;
    width: 80px; /* Adjust the width as needed */
    background-color: rgba(198, 158, 112, 0.2);
    border: none;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }

  .myday_content_down_right button:hover {
    background-color: rgba(198, 158, 112, 0.4); /* Change the background color on hover */
  }
  .task_edit
  {
    height: 40px;
    width: 50px;
    border: none;
    


  }
  .task_buttons
  {
    display: flex;
    align-items: center;
    gap: 20px;
  }

  .task_output
  {
    display: flex;
    gap: 20px;
  }
  .star_icon
  {
    height: 20px;
    width: 20px;
  }