  .mainHeader
  {
    max-width: 100vw;
    min-height: 48px;
    padding: 0px;
    background-color: #2564cf;
    margin: 0px;
    display: flex;
    align-items: center;
    justify-content: space-between;

      
  }
  .app_launcher_logo
  {
    width: 20px;
    height: 20px;
  }
  .todo
  {
    display: flex;
    align-items: center;
    gap: 20px;
    padding: 1px;
  
  }

  .todo_title
  {
    color: white;
    font-size: 18px;
    font-weight: 550;
    font-family:monospace ;
  }
  .toDo_center
  {
    width: 400px;
    height: 32px;
    padding: 3px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    border-radius: 6px;
    border: none;
  }
  .search_icon
  {
    width: 17px;
    height: 17px;
  }
  .toDo_right
  {
    display: flex;
    gap: 30px;
    cursor: pointer;
  }
  .settings_icon
  {
    width: 21px;
    height: 21px;
  }
  
  